* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.signup-form {
  width: 600px;
  height: 600px;
  border: 1px solid #717171;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.signup-form__inputs {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding: 45px 0;
  display: flex;
}

.signup-form__title {
  text-transform: uppercase;
  color: #a6a6a6;
  margin-top: 20px;
  font-family: Exo2, sans-serif;
  font-size: 32px;
  font-weight: 700;
}

.signup-form__inputs-item {
  min-width: 505px;
  justify-content: space-between;
  display: flex;
}

.signup-form__label {
  width: 180px;
  height: 36px;
  color: #717171;
  border: 1px solid #a6a6a6;
  border-radius: 5px;
  padding-left: 10px;
  font-family: Exo2;
  font-size: 16px;
  font-weight: 300;
  line-height: 36px;
  display: inline-block;
}

.signup-form__input {
  width: 310px;
}

.signup-form__button {
  width: 100%;
  height: 64px;
  text-transform: uppercase;
  color: #a6a6a6;
  background-color: silver;
  border: none;
  font-family: Exo2, sans-serif;
  font-size: 32px;
  font-weight: 700;
}

